import React from 'react';
import './topnavstyle.css';
import Icon from 'react-icons-kit';
import { user } from 'react-icons-kit/fa/user'





function Topnav() {

  const logout = () => {
    JSON.parse(localStorage.removeItem('auth'));
  }
  return (
  
      <div className="home">
        <div>
          <b><h3 className="cmp cms" >Compliant management system</h3></b>
        </div>
        <div className="cmp1">
          <a className="cmp cmp1" href="/Login" onClick={logout} title="Logout">
            <Icon size={22} icon={user} />
          </a>
        </div>
      </div>
  )
}

export default Topnav;